  methods: {
    submit() {
      this.$refs.form.validate();
      this.rankCal();
      if (this.percentile === null || this.percentile === "") {
        this.rankMessage = "";
      } else {
        this.rankMessage = "Expected CRL : " + this.your_rank;
      }
      this.ratingCal(this.your_rank);
    },
    reset() {
      this.your_rank = null;
      this.rating = 0;
      this.rankMessage = "";
      this.$refs.form.reset();
      this.$refs.form.resetValidation();
    },
    rankCal() {
      if (this.percentile === null || this.percentile === "") {
        this.your_rank = null;
        this.rankMessage = "";
      } else if (Number(this.percentile) === 100) {
        this.your_rank = 1;
      } else {
        this.your_rank = Math.round(
          (100 - this.percentile) * (this.students / 100)
        );
      }
    },
    ratingCal(rank) {
      if (rank < 5000) {
        this.rating = 5;
      } else if (rank < 30000) {
        this.rating = 4;
      } else if (rank < 100000) {
        this.rating = 3;
      } else if (rank < 250000) {
        this.rating = 2;
      } else if (rank < 500000) {
        this.rating = 1;
      } else {
        this.rating = 0;
      }
    },
    rankRangeCal(val1,val2) {
      return (
        Math.round((val1 - 99) * (this.students / 100)) +
        " - " +
        Math.round((val2 - 98) * (this.students / 100))
      );
    },
